<template>
  <v-container fill-height>
    <v-layout
      text-xs-center      
      fill-height
      align-center
      justify-center
      row
      wrap
    >
      <v-flex xs10>
        <v-card>
          <v-toolbar color="light-blue" dark>
            <v-toolbar-side-icon></v-toolbar-side-icon>

            <v-toolbar-title>local files</v-toolbar-title>

            <v-spacer></v-spacer>

            <v-btn icon>
              <v-icon>search</v-icon>
            </v-btn>

            <v-btn icon>
              <v-icon>view_module</v-icon>
            </v-btn>
          </v-toolbar>

          <v-list two-line subheader>
            <v-subheader inset>Files</v-subheader>
            
            <v-divider inset></v-divider>

            <v-list-tile
              v-for="item in fileData"
              :key="item.title"
              avatar
              @click="getData(item.data)"
            >
              <v-list-tile-avatar>
                <v-icon :class="[item.iconClass]">{{ item.icon }}</v-icon>
              </v-list-tile-avatar>

              <v-list-tile-content>
                <v-list-tile-title>{{ item.title }}</v-list-tile-title>
                <v-list-tile-sub-title>{{ item.subtitle }}</v-list-tile-sub-title>
              </v-list-tile-content>

              <v-list-tile-action>
                <v-btn icon ripple>
                  <v-icon color="grey lighten-1">info</v-icon>
                </v-btn>
              </v-list-tile-action>
            </v-list-tile>
          </v-list>
        </v-card>
      </v-flex>
      <v-flex xs10>
        <v-textarea
          solo
          :value="JSON.stringify(item)"
        ></v-textarea>
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
export default {
  data: () => ({
    fileData: [],
    item:[]
  }),
  mounted() {
    this.fileRead(require.context(`@/assets`, true, /\.(json|png|jpe?g|svg)$/i));
  },
  methods:{
    fileRead(r){
      // console.log("r",r);
      r.keys().forEach(key => (
        this.fileData.push(
          {
            data: r(key),
            title: key,
            icon: 'insert_drive_file',
            iconClass:'blue white--text'
          }
        )
      ));
      console.log(this.fileData);
    },
    getData(data){
      this.item = data;
      console.log("this.item",this.item);
    }
  }
}
</script>

<style>
</style>
